.portfolio {
  background-color: lightskyblue;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.portfolioH1 {
  font-size: 50px;
  height: 150px;
}

.portfolioUl {
  margin: 10px;
  padding: 0;
  list-style: none;
  display: flex;
  height: 50px;
  overflow: hidden;
}

.container {
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  height: auto;
}

.item {
  width: 220px;
  height: 150px;
  border-radius: 20px;
  border: 1px solid rgb(240, 239, 239);
  margin: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  position: relative;
  transition: all 0.5s ease;
  cursor: pointer;
}

.portfolioH3 {
  position: absolute;
  font-size: 20px;
}

.portfolioImg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}

.item:hover .portfolioImg {
  background-color: #15023a;
  opacity: 0.2;
  z-index: 0;
}
